.search-container{
    width: 40%;
    margin: auto;
    padding-top: 5%;
}

.search-container h2{
    text-align: center;
    padding-bottom: 1rem;
    
}

@media screen and (max-width:412px){
    .search-container{
        width: 50%;
        margin: auto;
        padding-top: 2%;
    }
    
    .search-container h2{
        text-align: center;
        padding-bottom: 0.5rem;
        
    }
}